<template>
  <div class="dashboard">
    <h3>Категории животных</h3>

    <div class="dashboard__params">
      <el-row class="dashboard__filter-row" :gutter="20">
        <el-col :span="12">
          <el-select v-model="shelter" clearable placeholder="Приют">
            <el-option v-for="item in shelters" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-col>

        <el-col :span="4">
          <el-select v-model="type" clearable placeholder="Тип животного">
            <el-option
              v-for="item in params.animalCategories"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-col>

        <el-col :span="4">
          <el-select v-model="sex" clearable placeholder="Пол животного">
            <el-option v-for="item in params.sexOptions" :key="item.value" :label="item.label" :value="item.value">
              {{ item.label }} <i :class="item.icon"></i>
            </el-option>
          </el-select>
        </el-col>

        <el-col :span="4">
          <el-button plain type="primary" icon="el-icon-search" @click="search">Искать</el-button>
        </el-col>
      </el-row>
    </div>

    <div class="dashboard__list-wrapper">
      <ul
        class="dashboard__list"
        v-infinite-scroll="fetch"
        infinite-scroll-disabled="infiniteScrollDisabled"
        v-if="animalsList.length > 0"
      >
        <v-animal-card v-for="(item, index) in animalsList" :key="index" :animal="item"></v-animal-card>
      </ul>
      <p class="dashboard__list-info" v-if="loading"><i class="el-icon-loading"></i> Загрузка...</p>
      <p class="dashboard__list-info" v-if="isAllLoaded">Все данные загружены</p>
    </div>
  </div>
</template>

<script>
import VAnimalCard from '@/components/VAnimalCard.vue'
import { FETCH_SHELTERS, FETCH_ANIMALS, MODULE_NAME as ANIMALS_MODULE } from '@/store/modules/animals'
import params from '@/helpers/params.json'

export default {
  name: 'Dashboard',
  components: { VAnimalCard },
  data() {
    return {
      params,
      shelter: null,
      type: null,
      sex: null
    }
  },
  computed: {
    module() {
      return this.$store.state[ANIMALS_MODULE]
    },
    loading() {
      return this.module.loading
    },
    shelters() {
      return this.module.model.shelters
    },
    animalsList() {
      return this.module.list
    },
    isAllLoaded() {
      return !this.loading && this.module.params.offset === this.module.params.count
    },
    infiniteScrollDisabled() {
      return this.loading || this.isAllLoaded
    }
  },
  created() {
    this.$store.dispatch(FETCH_SHELTERS)
    this.fetch()
  },
  methods: {
    fetch(options = {}) {
      const { shelter, type, sex } = this
      this.$store.dispatch(FETCH_ANIMALS, {
        shelter,
        type,
        sex,
        ...options
      })
    },
    search() {
      this.fetch({ offset: 0 })
    }
  }
}
</script>

<style lang="scss">
.dashboard {
  display: flex;
  flex-direction: column;

  &__filter-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
    .el-col > * {
      width: 100%;
    }
  }
  &__list-wrapper {
    overflow: auto;
    margin-top: 16px;
    padding-bottom: 32px;
  }
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }
}
</style>
